import { state } from '@angular/core';
import { ValidatorFn, AbstractControl } from '@angular/forms';

/* Validations class*/
export class ValidatorGenerator {

    static getAngularFormValidationFn(validationFunction: ValidationFn, eToken: string): ValidatorFn {
        return (c: AbstractControl) => {
            let validationResult = validationFunction(c);

            if (!validationResult) {
                return validationResult;
            }

            if (validationResult instanceof Object && eToken) {
                validationResult['errorToken'] = eToken;
            }
            return validationResult;
        };
    }

    static getFormValidationFn(validationFunction: ValidationFn, eToken: string): ValidatorFn {
        return (c: AbstractControl) => {
            let value = c.value;

            let validationResult = validationFunction(value);

            if (!validationResult) {
                return validationResult;
            }

            if (validationResult instanceof Object && eToken) {
                validationResult['errorToken'] = eToken;
            }
            return validationResult;
        };
    }

    static minLength(minLengthValue): ValidationFn {
        return (value: any): { [key: string]: any } => {
            let valueAsString = String(value);

            if (valueAsString.length < minLengthValue) {
                return {
                    minlength: {
                        "requiredLength": minLengthValue,
                        "actualLength": valueAsString.length
                    }
                };
            }

            return null;
        };
    }

    /**
     * Decimal Validator
     * @return {(control: AbstractControl): { [key: string]: any }} Validation function;
     */
    static maxDecimalLength(decimalCount: number): ValidationFn {
        return (value: any): { [key: string]: any } => {
            let valueAsString = String(value);
            if (value === null || value === undefined || valueAsString === "") {
                return null;
            }

            if (+decimalCount === 0 && valueAsString.indexOf(".") !== -1) {
                return { "maxDecimalLength": decimalCount };
            }

            let decimalStringValue = valueAsString.replace(new RegExp(/^([0-9]*.)/), "");
            return decimalStringValue !== "0" && decimalStringValue.length > decimalCount ? { "maxDecimalLength": decimalCount } : null;
        };
    }

    static minValue(minValue: number): ValidationFn {
        return (value: any): { [key: string]: any } => {
            if (value === null || value === undefined || value === "") {
                return null;
            }

            return Number(value) < Number(minValue) ? { 'minValue': { 'minValue': minValue, 'actualvalue': value } } : null;

        };
    }

    static maxValue(maxValue: number): ValidationFn {
        return (value: any): { [key: string]: any } => {
            if (value === null || value === undefined || value === "") {
                return null;
            }

            return Number(value) > Number(maxValue) ? { 'maxValue': { 'maxValue': maxValue, 'actualvalue': value } } : null;
        };
    }

    /**
     * Email validator.
     * @return {string} Error map.
     * @return {null} Validation has passed.
     */
    static emailValidation(value: string): IValidationResult {
        // tslint:disable-next-line:max-line-length
        let email = /[a-z0-9!#$%&'*+/=?^_`{|}~-]+(?:\.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)*@(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?/;

        if (value && value !== "" && !value.match(email)) {
            return { "email": true };
        }

        return null;
    }

    /**
     * Text validator.
     * @return {string} Error map.
     * @return {null} Validation has passed.
     */
    static textValidation(value: string): IValidationResult {
        let textRegExp = /^[a-zA-Z ]*$/;

        if (value !== "" && !textRegExp.test(value)) {
            return { "text": true };
        }

        return null;
    }
    /**
     * Number validator.
     * @return {string} Error map.
     * @return {null} Validation has passed.
     */
    static numberValidation(value: any): IValidationResult {
        let valueAsString = String(value);
        if (value !== undefined && value !== null && valueAsString !== "" &&
            (isNaN(value) ||
                valueAsString[0] === '.' ||
                valueAsString[valueAsString.length - 1] === '.')) {
            return { "number": true };
        }

        if (value !== undefined && value !== null && valueAsString !== "") {
            let indexOfDash = valueAsString.indexOf('-');
            let numberOfDashes = (valueAsString.match(/-/g) || []).length;
            let numberOfZerosAfterDash = (valueAsString.match(/-0[0]*/g) || []).length;
            let hasZeroAfterDash = numberOfZerosAfterDash > 0 && !(numberOfZerosAfterDash === 1 && valueAsString[2] === '.');
            if ((indexOfDash !== 0 && indexOfDash !== -1) ||
                numberOfDashes > 1 ||
                hasZeroAfterDash ||
                valueAsString.indexOf('.-') !== -1 ||
                valueAsString.indexOf('-.') !== -1) {
                return { "number": true };
            }
        }

        return null;
    }

    /**
     * Integer validation
     * @return {(control: AbstractControl): { [key: string]: any }} Validation function;
     */
    static integer(value: string): IValidationResult {
        if (ValidatorGenerator.numberValidation(value) !== null) {
            return {
                'integer': true
            };
        }

        let zeroDecimalCount = ValidatorGenerator.maxDecimalLength(0);
        if (zeroDecimalCount(value) !== null) {
            return {
                'integer': true
            };

        }

        return null;
    }

    /**
     * Password validator.
     * @return {string} Error map.
     * @return {null} Validation has passed.
     */
    static passwordValidation(value: string): IValidationResult {
        // {6,100}           - Assert password is between 6 and 100 characters
        // (?=.*[0-9])       - Assert a string has at least one number
        let passwordRegex = /^(?=.*[0-9])[a-zA-Z0-9!@#$%^&*]{6,100}$/;

        if (!value.match(passwordRegex)) {
            return { 'password': true };
        }

        return null;
    }
}

/* Interface for error map */
export interface IValidationResult {
    [key: string]: boolean;
}

export interface ValidationFn {
    (value: any): {
        [key: string]: any;
    };
}



