import './rx-extensions';

import { NgModule, ModuleWithProviders } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { CommonModule } from '@angular/common';
import { Md2MultiselectModule, Md2DatepickerModule } from 'md2';

import { MaterialModule } from '@angular/material';

import { CORE_COMPONENTS } from './components';
import { CORE_DIRECTIVES } from './directives';
import { CORE_SERVICES } from './services';



@NgModule({
    imports: [CommonModule, FormsModule, ReactiveFormsModule, MaterialModule.forRoot(),
        Md2MultiselectModule.forRoot(), Md2DatepickerModule.forRoot()
    ],
    exports: [CommonModule, FormsModule, ReactiveFormsModule, MaterialModule, ...CORE_COMPONENTS, ...CORE_DIRECTIVES],
    declarations: [...CORE_COMPONENTS, ...CORE_DIRECTIVES],
})
export class CoreRootModule { }

// TODO fix Core Module to work without MaterialModule.forRoot()
@NgModule({
    imports: [CommonModule, FormsModule, ReactiveFormsModule, MaterialModule,
        Md2MultiselectModule, Md2DatepickerModule
    ],
    exports: [CommonModule, FormsModule, ReactiveFormsModule, MaterialModule, ...CORE_COMPONENTS, ...CORE_DIRECTIVES],
    declarations: [...CORE_COMPONENTS, ...CORE_DIRECTIVES],
    providers: [...CORE_SERVICES],
})
export class CoreModule {
    public static forRoot(): ModuleWithProviders {
        return {
            ngModule: CoreRootModule,
            providers: [...CORE_SERVICES]
        };
    }
}



