import {
    trigger,
    state,
    style,
    transition,
    animate
} from '@angular/core';

export const fieldHintAnimation = [
    trigger('fieldHintAnimation', [
        transition('* => void', [
            animate('100ms ease-in', style({ transform: 'translateY(-100%)' }))
        ]),
        transition('void => *', [
            style({ transform: 'translateY(-100%)' }),
            animate('100ms ease-out')
        ])
    ])
];

export const genericFormAnimations = [
    ...fieldHintAnimation
];



