import { IHint } from './../generic-form/generic-form-elemets.interfaces';
import {
    Component, OnInit, Input, Output, EventEmitter, OnDestroy, ViewChild, HostListener,
    ElementRef, Optional, ViewEncapsulation, QueryList, Self, ViewChildren, ContentChild
} from '@angular/core';
import { ControlValueAccessor, NgControl, FormControl } from '@angular/forms';

import {
    MdOption, ConnectedOverlayDirective,
    Dir, transformPlaceholder,
    transformPanel, fadeInContent
} from '@angular/material';
import { ViewportRuler } from '@angular/material/core/overlay/position/viewport-ruler';

import { Subscription } from 'rxjs/Subscription';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';
import { Subject } from 'rxjs/Subject';

import { SearchState, SearchingService, IBackend, SearchStates } from './../../services/searching.service';
import { getValueAccessorProviders } from './../../models/custom-value-accessor.builder';
import { fieldHintAnimation } from '../generic-form/generic-form.animations';

import { AutocompleteConfiguration } from './autocomplete-config.model';
import { AutocompleteOverlay } from './autocomplete.overlay';

@Component({
    selector: 'ngt-autocomplete',
    templateUrl: './autocomplete.html',
    encapsulation: ViewEncapsulation.None,
    styleUrls: ['./autocomplete.scss'],
    providers: [
        getValueAccessorProviders(AutocompleteComponent)
    ],
    animations: [
        transformPlaceholder, transformPanel, fadeInContent, ...fieldHintAnimation
    ]
})
export class AutocompleteComponent extends AutocompleteOverlay implements OnInit, OnDestroy, ControlValueAccessor {
    /** Placeholder to be shown if no value has been selected. */
    @Input() iconType: string = ''; // 'prefix', 'suffix' or 'placeholder-prefix', 'placeholder-suffix'
    @Input() icon: string = '';
    @Input()
    set hint(value) {
        this._autocompleteHint = value || { align: "start", value: "" };
    }
    @Input() errors: boolean = false;
    @Input() get placeholder() { return this._placeholder; }
    set placeholder(value: string) { this._setPlaceholder(value); }
    @Input() autocompleteConfigurtation: AutocompleteConfiguration;
    @Input() backend: IBackend;
    @ViewChild(ConnectedOverlayDirective) overlayDir: ConnectedOverlayDirective;
    /** Trigger that opens the select. */
    @ViewChild('triggerRef', { read: ElementRef }) trigger: ElementRef;
    @ViewChildren(MdOption) options: QueryList<MdOption>;

    @Output() onSelectionChange: EventEmitter<any> = new EventEmitter<any>();
    @Output() onAutocompleteStateChange: EventEmitter<SearchState> = new EventEmitter<SearchState>();
    @Output() blur: EventEmitter<FocusEvent> = new EventEmitter<FocusEvent>();
    @Output() focus: EventEmitter<FocusEvent> = new EventEmitter<FocusEvent>();

    get value() { return this._inputValue; }
    set value(value: any) { this._setInputValue(value); };

    searchState: SearchState = null;

    /** Whether or not the overlay panel is open. */
    private _panelOpen = false;
    private _placeholder: string;

    private _inputValue: string = '';
    private _focused: boolean = false;
    private _disabled: boolean = false;
    private _autocompleteHint: IHint;

    private onSearchStateChange: BehaviorSubject<SearchState>;
    private onModelChangeSubject: Subject<any> = new Subject<any>();
    private subscriptions: Subscription[] = [];
    private optionsSubscriptions: Subscription[] = [];


    constructor(
        private searchBuilder: SearchingService,
        _viewportRuler: ViewportRuler,
        @Optional() _dir: Dir
    ) {
        super(_viewportRuler, _dir);
    }

    ngOnInit() {
        this.onSearchStateChange = this.searchBuilder
            .createSearchObservable(this.onModelChangeSubject, this.autocompleteConfigurtation.searchConfig, this.backend);
        this.setSearchStateChangeSubscription();
    }

    setSearchStateChangeSubscription() {
        this.subscriptions.push(
            this.onSearchStateChange.subscribe(newState => {
                this.searchState = newState;
                this.evaluateViewBasedOnSearchState();
                this.onAutocompleteStateChange.emit(newState);
            })
        );
    }

    evaluateViewBasedOnSearchState() {
        this.setHintValueBasedOnState();

        if (this.searchState.state !== SearchStates.SEARCH_DONE || !this.searchState.isQueryLengthValid) {
            this._panelOpen = false;
            return;
        }

        this.openPanelIfStateIsValid();
    }

    openPanelIfStateIsValid() {

        this._checkOverlayWithinViewport();
        this._placeholderState = this._isRtl() ? 'floating-rtl' : 'floating-ltr';
        if (!this._panelOpen) {
            this._panelOpen = true;
        }
    }

    onModelChange(inputValue) {
        this.value = inputValue;
        this._onChangeCallback(this.value);
        this._activateSearch(this.value);
    }

    setOptionsSubscriptions() {
        if (this.optionsSubscriptions) {
            this.optionsSubscriptions.forEach(e => { e.unsubscribe(); });
            this.optionsSubscriptions = [];
        }

        this.options.forEach((option: MdOption, index: number) => {
            const sub = option.onSelect.subscribe(event => {
                this.onOptionSelect(this.searchState.responseObject[index]);
                this._panelOpen = false;
            });
            this.optionsSubscriptions.push(sub);
        });
    }

    // From ControlValueAccessor interface
    // the ngModel init or form write value
    writeValue(value: any) {
        this.value = value;
    }

    // From ControlValueAccessor interface
    registerOnChange(fn: any) {
        this._onChangeCallback = fn;
    }

    // From ControlValueAccessor interface
    registerOnTouched(fn: any) {
        this._onTouchedCallback = fn;
    }

    setDisabledState(isDisabled: boolean) {
        this._disabled = isDisabled;
    }

    closePanel() {
        this._panelOpen = false;
        this.optionsSubscriptions.forEach(v => {
            v.unsubscribe();
        });

        this.optionsSubscriptions = [];
    }

    ngOnDestroy() {
        this.subscriptions.forEach(val => val.unsubscribe());
        this.subscriptions = [];
        this.onModelChangeSubject.unsubscribe();
    }

    onOptionSelect(option) {
        this.value = option[this.autocompleteConfigurtation.listMapping.visibleField];
        this._onChangeCallback(this.value);
        this.onSelectionChange.emit(option);
    }

    keydown() {
        // TODO implement keyup settings
    }

    private _activateSearch(value) {
        if (this._disabled || !this.focus) {
            return;
        }

        this.onModelChangeSubject.next(value);
    }

    private _handleFocus($event) {
        this._focused = true;

        if (this.autocompleteConfigurtation.activateOnFocus) {
            this._activateSearch(this.value);
        }
        this.focus.emit($event);
    }

    private _handleBlur(event) {
        // the pane will not close on blur since is causing problems with the selection
        this._focused = false;
        this._onTouchedCallback();
        this.blur.emit(event);

        if (this.isOptionClickEvent(event)) {
            this._panelOpen = false;
        }
    }

    private _isRtl(): boolean {
        return this._dir ? this._dir.value === 'rtl' : false;
    }

    private _setPlaceholder(value) {
        this._placeholder = value;
        // Must wait to record the trigger width to ensure placeholder width is included.
        Promise.resolve(null).then(() =>
            this._triggerWidth = this._getTriggerRect().width - 32
        );
    }

    private _setInputValue(value) {
        if (String(value) !== String(this._inputValue)) {
            this._inputValue = String(value);
        }
    }

    private getFirstError(control: FormControl) {
        return control.errors[Object.keys(control.errors)[0]];
    }

    private setHintValueBasedOnState() {
        if (!this.autocompleteConfigurtation || !this.autocompleteConfigurtation.statesMessages) {
            return;
        }

        switch (this.searchState.state) {
            case SearchStates.INITIAL:
                this._autocompleteHint.value = this.autocompleteConfigurtation.statesMessages.initialMessage || "";
                break;
            case SearchStates.TYPING:
                this._autocompleteHint.value = this.autocompleteConfigurtation.statesMessages.typingMessage || "";
                break;
            case SearchStates.SEARCHING:
                this._autocompleteHint.value = this.autocompleteConfigurtation.statesMessages.searchingMessage || "";
                break;
            case SearchStates.SEARCH_DONE:
                this._autocompleteHint.value = !this.searchState.isQueryLengthValid
                    ? this.autocompleteConfigurtation.statesMessages.invalidSearchLengthMessage || ""
                    : this.autocompleteConfigurtation.statesMessages.initialMessage || "";
                break;
        }
    }

    private isOptionClickEvent(event) {
        return event &&
            event['relatedTarget'] &&
            event['relatedTarget']['outerHTML'] &&
            String(event['relatedTarget']['outerHTML']).indexOf('md-option') === -1;
    }

    private _onChangeCallback(_: any) { }
    private _onTouchedCallback() { }
}



