import { Injectable } from '@angular/core';

@Injectable()
export class LoggerService {
    logError(error: Error) {
        if (process.env.ENV === 'production') {
            return;
        }

        console.log(error);
        console.log('ERROR STACK TRACE:');
        console.log(error.stack);
    }

    logWarning(warn) {
        if (process.env.ENV === 'production') {
            return;
        }

        console.warn(warn);
    }

    log(message) {
        if (process.env.ENV === 'production') {
            return;
        }

        console.log(message);
    }
}



