import { SearchState } from './../../services/searching.service';
import { ElementRef, Optional } from '@angular/core';
import { Dir } from '@angular/material';
import { ViewportRuler } from '@angular/material/core/overlay/position/viewport-ruler';


import {
    SELECT_OPTION_HEIGHT, SELECT_PANEL_PADDING_Y,
    SELECT_OPTION_HEIGHT_ADJUSTMENT, SELECT_MAX_OPTIONS_DISPLAYED,
    SELECT_PANEL_VIEWPORT_PADDING, SELECT_PANEL_MAX_HEIGHT,
    MAX_TRYES_TO_SET_CONTAINER_HEIGHT, SELECT_TRIGGER_HEIGHT
} from './autocomplete.consts';

export class AutocompleteOverlay {
    // Declare this using @ViewChild('triggerRef', { read: ElementRef })
    protected trigger: ElementRef;

    // Used to compute the overlay height
    protected searchState: SearchState = null;

    /**
     * The x-offset of the overlay panel in relation to the trigger's top start corner.
     * This must be adjusted to align the selected option text over the trigger text when
     * the panel opens. Will change based on LTR or RTL text direction.
     */
    protected _offsetX = 0;

    /**
     * The y-offset of the overlay panel in relation to the trigger's top start corner.
     * This must be adjusted to align the selected option text over the trigger text.
     * when the panel opens. Will change based on the y-position of the selected option.
     */
    protected _offsetY = 0;

    /** The value of the select panel's transform-origin property. */
    // _transformOrigin: string = 'top';

    /** The animation state of the placeholder. */
    protected _placeholderState = '';

    /**
     * This position config ensures that the top "start" corner of the overlay
     * is aligned with with the top "start" of the origin by default (overlapping
     * the trigger completely). If the panel cannot fit below the trigger, it
     * will fall back to a position above the trigger.
     */
    protected _positions = [
        {
            originX: 'start',
            originY: 'top',
            overlayX: 'start',
            overlayY: 'top',
        },
        {
            originX: 'start',
            originY: 'bottom',
            overlayX: 'start',
            overlayY: 'bottom',
        },
    ];
    protected maxPanelHeight = `${SELECT_PANEL_MAX_HEIGHT}px`;

    /**
     * The width of the trigger. Must be saved to set the min width of the overlay panel
     * and the width of the selected value.
     */
    protected _triggerWidth: number;

    constructor(protected _viewportRuler: ViewportRuler,
        @Optional() protected _dir: Dir) {

    }
    /**
     * Checks that the attempted overlay position will fit within the viewport.
     * If it will not fit, tries to adjust the scroll position and the associated
     * y-offset so the panel can open fully on-screen. If it still won't fit,
     * sets the offset back to 0 to allow the fallback position to take over.
     */
    protected _checkOverlayWithinViewport(): void {
        const viewportRect = this._viewportRuler.getViewportRect();
        const triggerRect = this._getTriggerRect();

        const topSpaceAvailable = triggerRect.top - SELECT_PANEL_VIEWPORT_PADDING;
        const bottomSpaceAvailable =
            viewportRect.height -
            triggerRect.bottom -
            SELECT_PANEL_VIEWPORT_PADDING;

        const panelHeightTop = Math.abs(this._offsetY);
        const totalPanelHeight = this._getTotalPanelHeight();
        const panelHeightBottom = totalPanelHeight - panelHeightTop; // - triggerRect.height;
        this._setPanelMaxHeight(SELECT_PANEL_MAX_HEIGHT);

        if (bottomSpaceAvailable >= totalPanelHeight) {
            this._offsetY = SELECT_OPTION_HEIGHT;
        } else if (topSpaceAvailable >= totalPanelHeight) {
            this._offsetY = -1 * Math.abs(totalPanelHeight + SELECT_PANEL_VIEWPORT_PADDING);
        } else {
            this._ajustPanelToFitViewPort(triggerRect, topSpaceAvailable, bottomSpaceAvailable, totalPanelHeight);
        }
    }

    protected _getTriggerRect(): ClientRect {
        return this.trigger.nativeElement.getBoundingClientRect();
    }

    private _ajustPanelToFitViewPort(
        triggerRect: ClientRect,
        topSpaceAvailable: number,
        bottomSpaceAvailable: number,
        totalPanelHeight: number) {

        if (bottomSpaceAvailable > topSpaceAvailable) {
            this._offsetY = 0;
            this._setPanelMaxHeight(bottomSpaceAvailable);
        } else {
            this._offsetY = - Math.abs(topSpaceAvailable + triggerRect.height + SELECT_PANEL_VIEWPORT_PADDING);
            this._setPanelMaxHeight(topSpaceAvailable);
        }
    }

    private _getTotalPanelHeight() {
        let optionsLength = this.searchState &&
            this.searchState.responseObject.length &&
            this.searchState.responseObject.length > 0 ? this.searchState.responseObject.length : 1;
        return Math.min(optionsLength * SELECT_OPTION_HEIGHT, SELECT_PANEL_MAX_HEIGHT);
    }

    private _setPanelMaxHeight(value) {
        this.maxPanelHeight = `${value}px`;
    }
}



